/**
 * Основные стили
 **/
html {
  font-size: 16px; }

body {
  font-family: 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.app {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto; }

.hide {
  display: none; }

.wrap {
  min-width: 320px;
  max-width: 1200px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  height: 100vh;
  max-width: 100%;
  background: url(../img/bg.jpg) no-repeat;
  background-size: cover; }

body {
  height: 100%;
  max-width: 100%; }

h1 {
  margin: 0;
  padding: 20px 0;
  display: inline-block; }

.app {
  padding: 20px;
  margin: 0 auto;
  max-width: 600px;
  max-height: 500px;
  background-color: #fff;
  border-radius: 10px;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  left: 0; }

.app__header {
  width: 300px;
  margin: 0 auto;
  position: relative; }

.app__play {
  width: 300px;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto; }

.game {
  background: url(../img/gif.gif) center center;
  background-size: cover;
  width: 300px;
  height: 300px;
  position: relative; }

#start {
  position: absolute;
  height: 50px;
  width: 50px;
  cursor: pointer;
  z-index: 1; }

#settings {
  position: absolute;
  height: 45px;
  width: 45px;
  right: 10px;
  top: 12px;
  cursor: pointer; }

#clock {
  position: absolute;
  height: 50px;
  width: 50px;
  left: 30px;
  top: 40%; }

#complexity {
  position: absolute;
  height: 50px;
  width: 50px;
  left: 30px;
  top: 15%; }

#palette {
  position: absolute;
  height: 50px;
  width: 50px;
  left: 30px;
  top: 65%; }

.app__footer {
  height: 72px; }

.set .game {
  background: #fff;
  border: 1px solid #000; }

.set #start {
  display: none; }

#game__time {
  position: absolute;
  right: 20px;
  top: 40%;
  width: 180px;
  max-height: 2.8rem;
  padding: .8rem 1rem;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4;
  color: #000;
  background: #fff none;
  border: 1px solid #999;
  border-radius: 5px; }

#select {
  position: absolute;
  right: 20px;
  top: 30%;
  width: 180px;
  max-height: 2.8rem;
  padding: .8rem 1rem;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4;
  color: #000;
  background: #fff none;
  border: 1px solid #999;
  border-radius: 5px;
  top: 15%; }

#select__color {
  position: absolute;
  right: 20px;
  bottom: 10%;
  width: 180px;
  max-height: 2.8rem;
  padding: .8rem 1rem;
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  line-height: 1.4;
  color: #000;
  background: #fff none;
  border: 1px solid #999;
  border-radius: 5px;
  top: 65%; }
